import { delegate } from './../tools/utilities/event-tools';

const SELECTOR_CONTAINER = '[data-home-hero]';
const SELECTOR_SCROLL = '[data-home-hero-scroll]';
const SELECTOR_SECTION = '.shopify-section';

delegate({
  element: document,
  selector: SELECTOR_SCROLL,
  event: 'click',
  callback: (e,element) => {
    const container = element.closest(SELECTOR_SECTION);
    if(!container) return;
    const next = container.nextElementSibling;
    if(!next) return;
    next.scrollIntoView({ behavior: 'smooth' });
  }
})